@import "../../../../../assets/styles/settings/_settings.layout";
@import "../../../../../assets/styles/settings/_settings.colors";

:host /deep/ {
    .pattern.link-block {
        display: flex;
        flex-direction: column;
        text-align: center;
        align-items: center;
        justify-content: center;
        min-height: 130px;
        padding: 15px 5px;
        margin-bottom: 20px;
        color: $white;

        & > * {
          padding-bottom: 5px;
        }

        img {
          align-self: center;
        }

        .title {
          font-size: 18px;
          letter-spacing: 0.7px;
        }

        .description {
          font-size: 14px;
          letter-spacing: 0.5px;
          max-width:350px;
        }

        .link {
          font-size: 16px;
          letter-spacing: 0.6px;
          color: $white;
          text-decoration: none;
          font-weight: bold;

          .external-link-icon {
            width: 14px;
            height: 14px;
          }
        }
    }
}
